import {AfterViewInit, Component, OnDestroy, OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {dataService} from "../data.service";
declare let firebase:any
declare let $:any

@Component({
  selector: 'app-leader',
  templateUrl: './leader.component.html',

})
export class LeaderComponent implements AfterViewInit, OnDestroy{
  public datajun:any = []
  public datasen:any = []
  constructor(private data:dataService){
    this.data.loaderstart()

  }
ngOnDestroy(){
$(".babies").css({
    "background" : "url('assets/back.jpg') no-repeat center center fixed",
    "background-size" : "cover"
  })
}
  ngAfterViewInit(){

    firebase.database().ref().child("details").orderByChild("attempted").equalTo(true).on('value',(snap)=> {
      this.datasen = []
      this.datajun = []
      let value = Object.keys(snap.val()).map(k => {
        return snap.val()[k]
      })
      value.forEach(k => {
        if(k.year == "Junior")
        {
          this.datajun.push(k)
        }
        else {
          this.datasen.push(k)
        }
      })
      this.datajun = this.datajun.sort((a,b)=>{
        return a.marks - b.marks
      })
      this.datasen = this.datasen.sort((a,b)=>{
        return a.marks - b.marks
      })
      this.data.loaderremove()
      $(".babies").css({
        "background" : "url('assets/back1.png') no-repeat center center fixed",
        "background-size" : "cover"
      })
    })

  }



}



