import {AfterViewInit, Component, HostListener, OnDestroy, OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {dataService} from "../data.service";
import {Observable} from "rxjs/Observable";

declare let firebase:any
declare let hljs:any
declare let $:any

@Component({
  selector: 'app-ques',
  templateUrl: './ques.component.html',
  styleUrls: ['./ques.component.css']
})
export class QuesComponent implements OnDestroy,AfterViewInit{
  public questionss:any = []
  public questions:any =[]
  public count = 0
public finalcount = 0
  ngAfterViewInit(){


  }

  constructor(private router:Router,private data:dataService) {
this.data.loaderremove()


    this.data.getdata()

this.data.loaderremove()

  }
  ngOnDestroy()
  {
    $(".babies").css({
      "background" : "url('assets/back.jpg') no-repeat center center fixed",
      "background-size" : "cover"
    })

  }

checks(){
  if(!this.data.login )
  {
    return false;
  }
  else {
    $(".babies").css({
      "background" : "url('assets/final.png') no-repeat center center fixed",
      "background-color" : "#e4e6e6"

    })
    if(this.count == 0) {
     // $(document).ready(()=> {
     //
     //   window.onblur = ()=> {
     //     this.finalize()
     //   };
     // })
      this.data.loaderstart()

      firebase.storage().ref('new.txt').getDownloadURL().then((url)=> {

        $.ajax({
          url : url,
          type : "get",
          success : (data)=>{

            this.questionss = data.split("///")

            if(this.data.userdet.year == "Senior")
            {
              this.questionss = this.questionss.splice(0,30)
              console.log(this.questionss)
            }
            else {
              this.questionss = this.questionss.splice(30,25)

            }
          }
        })

      }).catch(function(error) {
        // Handle any errors
      });
console.log(this.data.userdet.year)
      firebase.database().ref("questions" + this.data.userdet.year).once('value', (snapshot) => {
        this.questions = Object.keys(snapshot.val()).map(k => {
          return snapshot.val()[k]

        })

        var cou = 0
        this.questions.forEach(k => {
          this.data.result[cou] = "None"
          cou = cou + 1

        })
        this.data.loaderremove()
        $(".babies").css({
          "background" : "url('assets/final.png') no-repeat center center fixed",
          "background-color" : "#e4e6e6"

        })
        let kg= setInterval(()=>{

          if($("pre code").length > 0)
          {
            $('pre code').each((i, e) => {

              hljs.highlightBlock(e)});

            clearInterval(kg)
          }

        },3000)


      })


      this.count = this.count + 1


         }
    return true;
  }
}
onchn(i,s)
{
this.data.result[i] =s
console.log(this.data.result)
}

finalize() {
this.data.loaderstart()
    firebase.database().ref("answers" + this.data.userdet.year).once('value', (snap) => {
      let results = Object.keys(this.data.result).map(k => {
        return this.data.result[k]
      })
      let marks = 0
      let cou = 0

      results.forEach(k => {

        if (k == snap.val()[cou]) {
          marks = marks + 1
        }
        cou = cou + 1
      })

      firebase.database().ref("details/" + this.data.userkey).update({
        "marks": marks,
        "attempted": true
      })
      this.data.loaderremove()


    })
}

}



