import {Injectable} from '@angular/core';
import {Router} from "@angular/router";
declare  let firebase:any;
declare let $:any


@Injectable()
export class dataService{
  public login:boolean = false;
  public userdet:any = {"attempted" : true}
  public userkey:any
  public result:any = {}

  public final:any = false
  constructor(private router: Router) {
    this.getdata()
  }


  getdata(){
    let user = firebase.auth().currentUser
    if(user)
    {
      firebase.database().ref().child("details").orderByChild("email").equalTo(user.email).on('value', (snap) => {

        this.userkey = Object.keys(snap.val()).map(k => {return k})[0]
        this.userdet = Object.keys(snap.val()).map(k => {
          return snap.val()[k]
        })[0]

      });
      this.login = true;
    }

  }

  auth()
  {
    if(firebase.auth().currentUser)
    {
      return true;
    }
    else {
      return false;
    }
  }
  logout() {



    firebase.auth().signOut().then(()=> {

      this.router.navigate(['../login'])
    }, (error:any) =>{
      console.log(error);
    });

  }
  loaderstart(){
    $(".contents").css({
      "display" : "none"
    })
$(".loader").css({
  "display" : "initial"
})
    $(".babies").css({
      "background" : "black no-repeat center center fixed",
      "background-size" : "cover"
    })
  }
  loaderremove(){
    $(".loader").css({
      "display" : "none"
    })
    $(".contents").css({
      "display" : "initial"
    })
    $(".babies").css({
      "background": "url('assets/back.jpg') no-repeat center center fixed",
      "background-size" : "cover"
    })
  }

}



